
"use client";

import * as React from 'react';
import { PageHeader } from "@/components/page-header";
import { CategoriesTable } from "@/components/dashboard/categories-table";
import type { Category } from "@/types";

export default function CategoriesPage() {
  const [categories, setCategories] = React.useState<Category[]>([]);

  React.useEffect(() => {
    try {
      const storedCategories = localStorage.getItem('inventopro-categories');
      if (storedCategories) {
        setCategories(JSON.parse(storedCategories));
      }
    } catch (error) {
        console.error("Failed to parse categories from localStorage", error);
    }
  }, []);

  React.useEffect(() => {
    localStorage.setItem('inventopro-categories', JSON.stringify(categories));
  }, [categories]);

  const handleAddCategory = (newCategory: Omit<Category, 'id' | 'itemCount'>) => {
    setCategories((prevCategories) => [
      ...prevCategories,
      { ...newCategory, id: `cat-${Date.now()}`, itemCount: 0 },
    ]);
  };

  const handleEditCategory = (updatedCategory: Category) => {
    setCategories((prevCategories) =>
      prevCategories.map((category) =>
        category.id === updatedCategory.id ? updatedCategory : category
      )
    );
  };

  const handleDeleteCategory = (categoryId: string) => {
    setCategories((prevCategories) =>
      prevCategories.filter((category) => category.id !== categoryId)
    );
  };
  
  return (
    <>
      <PageHeader
        title="Categories"
        description="Organize your items into categories."
      />
      <CategoriesTable 
        categories={categories} 
        onAddCategory={handleAddCategory}
        onEditCategory={handleEditCategory}
        onDeleteCategory={handleDeleteCategory}
      />
    </>
  );
}
